import React, { useContext, useState } from 'react'
import { Grid, Paper, Input, Typography, Button } from '@mui/material'


import './index.scss'
import { AuthContext } from '../../Contexts/AuthContext'
import { useNavigate } from 'react-router-dom'


export const LoginForm = () => {

  const [formData, setFormData] = useState({})
  const { signin } = useContext(AuthContext)
  const navigate = useNavigate()
  const inputProps = { 
    onChange: (inputValue) => { 
      setFormData((data) => {
        return {
          ...data,
          [inputValue.target.name]: inputValue.target.value,
        }
      })
    },
  }

  const login = () => {
    console.log(formData)
    signin({user: 'abc@salalah.com'}, ()=>{navigate('/')})
    //TODO: make api call with form data and show error or success message
  }

  return <Paper className="form-wrapper">
    <Grid container spacing={2} className="form-container">
      <Grid item xs={12}>
        <Typography variant="h6" align="left">
          Log in
        </Typography>
      </Grid>
      <Grid item container spacing={0} xs={12}>
        <Input fullWidth size="small" name="username" placeholder="Username" inputProps={inputProps} />
      </Grid>
      <Grid item container spacing={0} xs={12}>
        <Input type="password" fullWidth size="password" name="returnDate" placeholder="Password" inputProps={inputProps} />
      </Grid>
      <Grid item container spacing={0} xs={12}>
        <Button variant="contained" onClick={login}>Login</Button>
      </Grid>
   </Grid>
  </Paper>
}