import React from 'react'
import { Grid, Avatar, Typography, Menu, MenuItem } from '@mui/material'
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';



import './index.scss'


export const UserProfile = () => {
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleMenu = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return <Grid container spacing={2} alignItems="center" justifyContent='flex-end' className='user-space'>
    <Grid item onClick={handleMenu}>
      <Avatar alt="Remy Sharp" src=""/>
    </Grid>
    <Grid item onClick={handleMenu}>
      <Typography variant="body2" align="right">John Doe</Typography>
    </Grid>
    <Grid item alignContent='center' onClick={handleMenu}>
      <ArrowDropDownIcon/>
    </Grid>
    <Menu
        id="menu-appbar"
        anchorEl={anchorEl}
        anchorOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}
        keepMounted
        transformOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        <MenuItem onClick={handleClose}>Sign Out</MenuItem>
      </Menu>
  </Grid>
}