import React from 'react'
import { FixedSizeList } from 'react-window'
import { ListItem, Grid, Paper, Typography, Box } from '@mui/material'

import './index.scss'
import "react-datepicker/dist/react-datepicker.css";
import { passengers } from '../../../mocks/passengers'



export const DataList = ({title='home', data=passengers}) => {

  function renderRow(props) {
    const { index, style } = props;
    const { firstName, lastName, id, leadDestination, leadStatus} = data[index];
  
    return (
      <ListItem style={style} key={index} component="div" disablePadding className={`List-item ${leadStatus}`}>
        <div>
          <Typography variant='h4'>
            {firstName} {lastName}
          </Typography>
          <Typography variant='body2'>
            {leadDestination}
          </Typography>
        </div>
      </ListItem>
    );
  }

  return (
    <Box className="List-container">
      {/* <Typography variant="h3" marginLeft={3} align="left">{title}</Typography> */}
      <FixedSizeList
        height={468}
        itemSize={50}
        itemCount={data.length}
        overscanCount={5}
      >
        {renderRow}
      </FixedSizeList>
    </Box>
  )
}
