import React, { useState } from 'react'
import { Grid, Paper, Input, Select, MenuItem, Typography, FormControl, Button } from '@mui/material'


import './index.scss'
import { PassengerDataSet } from '../../../Pages/AirTicket/passengerDataSet'


export const PassengerForm = () => {
  const [passengers, setPassengers] = useState([])
  const [formData, setFormData] = useState({})
  const inputProps = { 
    onChange: (inputValue) => { 
      setFormData((data) => {
        return {
          ...data,
          [inputValue.target.name]: inputValue.target.value,
        }
      })
    },
    className: 'salalah-input-box',
  }

  const submitForm = () => {
    setPassengers((data) => {
      return [...data, {...formData}]
    })
    setFormData([])
    //TODO: make api call with form data and show error or success message
  }

  return <Paper className="form-wrapper">
    <Grid container spacing={3} className="form-container">
      <Grid item xs={12}>
        <Typography variant="h3" align="left" paddingTop={0}>
          Passenger Details
        </Typography>
      </Grid>
      {passengers.map((item, index)=>{ return <PassengerDataSet data={item} index={index} />})}
      <Grid item xs={4}>
        <Input fullWidth value={formData.firstName} size="small" name="firstName" placeholder="First Name" inputProps={inputProps} />
      </Grid>
      <Grid item xs={4}>
        <Input fullWidth value={formData.lastName} size="small" name="lastName" placeholder="Last Name" inputProps={inputProps} />
      </Grid>
      <Grid item xs={4}>
        <FormControl fullWidth>
          <Select
            labelId="demo-simple-select-label"
            id="demo-simple-select"
            name="gender"
            value={formData?.gender || ''}
            label="Age"
            inputProps={inputProps}
            native="true"
            variant='filled'
          >
            <option value={'Male'}>Male</option>
            <option value={'Female'}>Female</option>
          </Select>
        </FormControl>
      </Grid>
      <Grid item container spacing={0} xs={4}>
        <Input fullWidth size="small" value={formData.phone} name="phone" placeholder="Phone Number" inputProps={inputProps} />
      </Grid>
      <Grid item container spacing={0} xs={4}>
        <Input fullWidth size="small" value={formData.email} name="email" placeholder="Email" inputProps={inputProps} />
      </Grid>
      <Grid item container spacing={0} xs={4}>
        <Input fullWidth size="small" value={formData.city} name="city" placeholder="City" inputProps={inputProps} />
      </Grid>
      <Grid item container spacing={0} xs={8}>
        <Input fullWidth size="small" value={formData.address} name="address" placeholder="Address" inputProps={inputProps} />
      </Grid>
      <Grid item container spacing={0} xs={4}>
        <Input fullWidth size="small" value={formData.pincode} name="pincode" placeholder="Pin code" inputProps={inputProps} />
      </Grid>
      <Grid item container spacing={0} xs={4}>
        <Input fullWidth size="small" value={formData.dob} name="dob" placeholder="Date of Birth" inputProps={inputProps} />
      </Grid>
      <Grid item container spacing={0} xs={4}>
        <Input fullWidth size="small" value={formData.document1} name="document1" placeholder="Upload Document" inputProps={inputProps} />
      </Grid>
      <Grid item container spacing={0} xs={4}>
        <Input fullWidth size="small" value={formData.document2} name="document2" placeholder="Upload Document" inputProps={inputProps} />
      </Grid>
      <Grid item container spacing={0} xs={12}>
        <Button variant="contained" onClick={submitForm}>Add Passenger</Button>
      </Grid>
    </Grid>
  </Paper>
}