import React from "react";
import { useRouteError } from "react-router-dom";

export const ErrorPage = () => {
  const error = useRouteError();
  console.error(error);

  return (
    <div id="error-page">
      <center>
      <h1>Page Not Found</h1>
      <p>
        <i>{error.statusText || error.message}</i>
      </p>
      </center>
    </div>
  );
}

